import React, { memo, useEffect, useState } from 'react'

import {PaginationWrapper} from "./style"

const Pagination = memo((props) => {

  // 接收父组件传参
  const { totalPage, handleChangeInd } = props
  // const totalPage = 55

  // 定义当前页面下标
  const [currentIndex, setCurrentIndex] = useState(1)

  // 定义总页面下标
  const pages = totalPage > 7 ? 7 : totalPage
  // 初始化分页器
  const [pagArr, setPagArr] = useState([])
  useEffect(() => {
    setPagArr(Array.from({length: pages},(_, i)=>1+(i)))
  }, [pages])

  // 控制分页器
  const handlePage = (item) => {
    // 当前页数<=4时，数组设置为1-7，当页数>4并且<总页数-3时，数组设置为[item-3, ... ,item, ..., item+3]，当页数>总页数-3时，数组设置为[总页数-6, ..., 总页数]
    // 不满7页时，显示具体的页数
    if(item === currentIndex) return
    let newArr = []
    switch(true) {
      case item <= 4: 
              newArr = Array.from({length:pages},(_, i)=>1+(i));break;
      case item > 4 && item <= (totalPage - 3):
              newArr = Array.from({length:pages},(_, i)=>item-3+(i));break;
      case item > (totalPage - 3):
              newArr = Array.from({length:pages},(_, i)=>totalPage-(i)).reverse();break;
      default: newArr = pagArr;break;
    }
    setPagArr(newArr)
    setCurrentIndex(item)
    // 触发父组件方法改变列表内容
    handleChangeInd(item-1)
  }
  
  return (
    <PaginationWrapper>
      <div className="pagination_btns">
        <div className={"btn_item " + (currentIndex === 1 ? 'disable' : '')} onClick={e => handlePage(1)}>First</div>
        {
          pagArr.map((item, index) => {
            return (
              <div 
                className={"btn_item nums " + (currentIndex === item ? 'active' : '')}
                key={index} 
                onClick={e => handlePage(item)}
              >{item}</div>
            )
          })
        }
        <div className={"btn_item " + (currentIndex === totalPage ? 'disable' : '')} onClick={e => handlePage(totalPage)}>Last</div>
      </div>
      <div className="pagination_index">{currentIndex}/{totalPage}</div>
    </PaginationWrapper>
  )
})

export default Pagination