import React, { memo, useEffect, useState } from 'react'
import { KnowledgeWrapper, NewsWrapper } from './style'
import data from "@/assets/data/knowledge/data.json"
import { ArrowRightOutlined } from '@ant-design/icons'
import Pagination from '@/components/pagination'
import { useNavigate } from 'react-router-dom'

const Knowledge = memo(() => {
  // 初始化列表
  const [currentList, setCurrentList] = useState([])
  useEffect(() => {
    setCurrentList(data.slice(0, 8))
  },[data])

  // 计算总页数
  const [currentPageInd, setCurrentPageInd] = useState(0)
  const totalPage = Math.ceil(data.length/8)
  // 改变分页器
  const handleChangeInd = (ind) => {
    setCurrentPageInd(ind)
    setCurrentList(data.slice(ind*8, ind*8+8))
  }
  // 点击跳转详情页
  const navigate = useNavigate()
  const handleToContent = (item, index) => {
    const prev = data[currentPageInd*8 + index - 1]?.title
    const next = data[currentPageInd*8 + index + 1]?.title
    navigate("/contents", {state: {params: { ...item, prev, next }}})
  }
  return (
    <KnowledgeWrapper>
      <ul className="news_wrap">
        {
          currentList.map((item, index) => {
            return (
              <li className="news_item" key={index}>
                <div className="img_wrap" onClick={e => handleToContent(item, index)}>
                  <img src={require("@/assets/data/knowledge/imgs/" + item.img)} alt="" />
                  <span>{item.date}</span>
                </div>
                <div className="word_wrap">
                  <p onClick={e => handleToContent(item, index)}>{item.title}</p>
                  <span>{item.des}</span>
                  <button onClick={e => handleToContent(item, index)}>Read More <ArrowRightOutlined /></button>
                </div>
              </li>
            )
          })
        }
      </ul>
      <Pagination
        totalPage={totalPage} 
        handleChangeInd={ind => handleChangeInd(ind)} 
      />
    </KnowledgeWrapper>
  )
})

export default Knowledge